<script>
import { ref, onMounted, watch, watchEffect, $emit } from "vue"
import { apiGetAbilityToAct } from "@/api/api"
import { useActStore } from '@/store/global'

export default {
    props: {
        actType_p: Object,
        otherAct_p: String
    },
    setup(props, { emit }) {
        const actStore = useActStore()
        let actType = ref([])
        let actTypeList = ref({})

        let isShow = ref(0)
        let actInput = ref("")
        const showActInput = () => {
            if (isShow.value == 0) {
                isShow.value = 1
            } else {
                isShow.value = 0
            }
        };

        const updateActType = () => {
            //去除數字以外的
            let actType_e = actType.value.map((x) => x.replace(/[^0-9]/gi, ""))
            emit("actType", actType_e)
        };
        const updateActInput = () => {
            emit("actInput", actInput.value)
        };

        onMounted(async () => {
            if (actStore.actTypeList == undefined) {
                // call api
                actTypeList.value = await apiGetAbilityToAct()
                actTypeList.value = actTypeList.value.data.data
                actStore.actTypeList = actTypeList.value
            } else {
                actTypeList.value = actStore.actTypeList
            }

            if (props.actType_p) {
                props.actType_p.forEach((item) => {
                    actType.value.push("act" + item.id)
                });
            }
            if (props.otherAct_p?.length > 0) {
                actInput.value = props.otherAct_p
                actType.value.push("act" + (actTypeList.value.length + 1))
                isShow.value = 1
            }
        })
        watch(
            () => actType.value,
            async () => {
                updateActType()
            }
        );
        watch(
            () => actInput.value,
            async () => {
                updateActInput()
            }
        )

        return { actType, isShow, showActInput, actTypeList, actInput }
    },
};
</script>

<template>
    <div class="mb-3">
        <label class="form-label">行動能力<span class="star">*</span></label>
        <div class="input-list" v-if="actTypeList.length > 0">
            <div class="form-check-inline" v-for="item in actTypeList" :key="item.id">
                <input type="checkbox" class="form-check-input" :value="'act' + item.id" :id="item.name"
                    v-model="actType" />
                <label class="form-check-label" :for="item.name">{{ item.name }}</label>
            </div>
            <div class="form-check-inline">
                <input type="checkbox" class="form-check-input" :value="'act' + (actTypeList.length + 1)"
                    :id="actTypeList.length + 1" v-model="actType" @change="showActInput" />
                <label class="form-check-label" for="act6">其他</label>
            </div>
        </div>
        <input type="text" class="form-control mt-1" placeholder="請輸入其他能力" v-model="actInput" v-if="isShow" />
    </div>
</template>

<style lang="scss" scoped>

</style>
