<script>
import { ref, onMounted, watch, $emit, toRef } from 'vue'
import { apiGetCityArea } from "@/api/api"
import { useCityStore } from '@/store/global'

export default {
    props: {
        cityIdx_p: Number || String || undefined,
        areaIdx_p: Number || String || undefined
    },
    setup(props, { emit }) {
        const cityStore = useCityStore()
        let cityAreaData = ref([])
        let cityIdx = ref(props.cityIdx_p)
        let areaIdx = ref(props.areaIdx_p)
        let cityList = ref([])
        let areaList = ref([])

        //往外傳
        const updateCityIdx = () => {
            emit("cityIdx", cityIdx)
        }
        const updateAreaIdx = () => {
            emit("areaIdx", areaIdx)
        }

        onMounted(async () => {
            if (cityStore.cityAreaList == undefined) {
                cityAreaData.value = await apiGetCityArea()
                cityAreaData.value = cityAreaData.value.data
                cityStore.cityAreaList = cityAreaData.value
            } else {
                cityAreaData.value = cityStore.cityAreaList
            }
            if (cityIdx.value != undefined) {
                areaList.value = cityAreaData.value[cityIdx.value - 1].areas
            }
        })


        watch(() => cityIdx.value, async () => {
            areaList.value = cityAreaData?.value?.[cityIdx.value - 1]?.areas
            updateCityIdx()
        })
        watch(() => areaIdx.value, async () => {
            updateAreaIdx()
        })
        watch(() => props.cityIdx_p , async () => {
            cityIdx.value = props.cityIdx_p
        })
        watch(() => props.areaIdx_p , async () => {
            areaIdx.value = props.areaIdx_p
        })


        return { cityAreaData, areaList, cityIdx, areaIdx }
    },
};
</script>

<template>
    <div class="input-group">
        <select class="col form-control form-select" v-model="cityIdx">
            <option value="undefined" disabled>請選擇縣市</option>
            <option v-for="item in cityAreaData" :value="item.id" :key="item.id">{{ item.city_name }}</option>
        </select>
        <select class="col form-control form-select" v-model="areaIdx">
            <option value="undefined" disabled>請選擇鄉鎮區</option>
            <option v-for="item in areaList" :value="item.id" :key="item.id">{{ item.area_name }}</option>
        </select>
    </div>
</template>


